.section {
  &__contact {
    background-color: $white;
    .container {
      padding: 80px 0;
      &:first-child {
        padding-bottom: 0;
      }
    }
  }
}
.contact {
  &__wrapper {
    position: relative;
  }
  &__maintitle {
    margin-bottom: 80px;
  }
  &__title {
    @include font("Quattrocento Sans", 20px, 700, $darkGray);
    text-transform: uppercase;
    line-height: 20px;
    margin-bottom: 35px;
  }
  &__text {
    width: 50%;
    span {
      display: block;
      font-size: 16px;
      color: $lightGray;
      line-height: 1.6;
      margin-bottom: 25px;
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
  &__form {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    flex-wrap: wrap;
    position: absolute;
    right: 0;
    bottom: 0;
    width: 50%;
    height: 400px;
    padding: 30px 30px 0 30px;
    background-color: $white;
  }
  &__button {
    display: block;
    border-color: $darkGray;
    color: $darkGray;
    &:hover {
      border-color: $white;
      color: $white;
    }
  }
}
.form {
  &__input {
    display: block;
    height: 45px;
    width: calc((100% - 15px) / 2);
    border: none;
    padding: 15px;
    margin-right: 15px;
    margin-bottom: 15px;
    background-color: $bg-input;
    border-radius: 6px;
    @include placeholder($gray, 0.7, 16px, "Quattrocento Sans");
    @include font("Quattrocento Sans", 16px, 700, $darkGray);
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    &:nth-of-type(2) {
      margin-right: 0;
    }
  }
  &__textarea {
    display: block;
    width: 100%;
    height: calc(100% - 120px);
    border: none;
    resize: none; 
    padding: 15px;
    margin-bottom: 15px;
    background-color: $bg-input;
    border-radius: 6px;
    @include placeholder($gray, 0.7, 16px, "Quattrocento Sans");
    @include font("Quattrocento Sans", 16px, 700, $darkGray);
    cursor: pointer;
    transition: all 0.3s ease-in-out;
  }
}